export const CANCEL_SELECT_NODE = Symbol('cancel_select_node');
export const COPY_SELECTED_NODES = Symbol('copy_SELECTED_NODES');
export const CUT_SELECTED_NODES = Symbol('cut_SELECTED_NODES');
export const DELETE_SELECTED_NODES = Symbol('delete_SELECTED_NODES');

export interface ISelectedInfo {
  isSequent: boolean;
  // 后续可能根据选择的节点类型添加相关的选项
  // nodeTypes: string[];
}

export const [copySelectedNodes, isCopySelectedNodes] = createActionHandlers<NodeSelectToolbarActionPayload>(COPY_SELECTED_NODES);
export const [cutSelectedNodes, isCutSelectedNodes] = createActionHandlers<NodeSelectToolbarActionPayload>(CUT_SELECTED_NODES);
export const [deleteSelectedNodes, isDeleteSelectedNodes] = createActionHandlers<NodeSelectToolbarActionPayload>(DELETE_SELECTED_NODES);
export const [cancelSelectNode, isCancelSelectNode] = createActionHandlers(CANCEL_SELECT_NODE);

export const actions = {
  copySelectedNodes,
  cutSelectedNodes,
  deleteSelectedNodes,
  cancelSelectNode,
};
